body {
	background: #f8f8f8 !important;
	margin: 0;
}

h1 {
	padding-left: 50px;
	font-family: "Dosis";
	color: #555;
	display: inline-block;
	width: 45%;
}

#root {
	width: 100% !important;
}

.DashboardHeader {
	height: 70px;
	position: fixed;
	width: 100%;
	background-color: #ffffff;
    -webkit-animation: fadeout 3s linear forwards;
    animation: fadeout 3s linear forwards;
}

.DashboardHeader:hover {
	opacity: 1;
    -webkit-animation: none;
    animation: none;
}

@-webkit-keyframes fadeout {
  0% { opacity: 1; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

@keyframes fadeout {
  0% { opacity: 1; }
  50% { opacity: 1; }
  100% { opacity: 0; }
}

.ToggleContainer {
	margin: 0px;
	display: inline-block;
	width: 50%;
}

.checkbox {
	float: right;
	display: inline-block;
	padding-right: 10px;
}

@font-face {
	font-family: "Dosis";
	src: url("./fonts/Dosis-Regular.ttf");
}


