.FeedContainer {
	margin: 5px;
	background: white;
	/*width: calc(100%/6.5);*/
	display: inline-block;
	vertical-align: top;
	height: 98vh;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	background-color: #ffffff;
	border-radius: 25px;
}

.FeedContent {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-positive: 1;
	    flex-grow: 1;
    overflow: auto;
	-webkit-box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	        box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
	word-wrap: break-word;
}

.FeedHeader {
	font-family: "Dosis","Open Sans",sans-serif;
	height: 75px;
	text-align: center;
    font-weight: 800;
    letter-spacing: 1px;
    line-height: 1.6;
}

.FeedHeader img {
	display: inline-block;
	width: 50px;
	height: 50px;
	vertical-align: middle;
	margin-bottom: 10px;
	margin-right: 25px;
}

.FeedHeader h2 {
	display: inline-block;
}



